@import 'global.scss';

:host {
  display: block;

  --_animation-duration: 300ms;
}

.animate-opacity {
  opacity: 0;
}

.wrapper {
  overflow: hidden;
  position: relative;
  transition: opacity var(--_animation-duration);

  &.visible {
    opacity: 1;

    .container {
      left: 0;
      top: 0;
    }
  }
}

.container {
  position: relative;
  animation-duration: var(--_animation-duration);
}

.scroll-in-slide {
  &-left {
    .container {
      left: -100%;
    }
  }

  &-right {
    .container {
      left: 100%;
    }
  }

  &-top {
    .container {
      top: -100%;
    }
  }

  &-bottom {
    .container {
      top: 100%;
    }
  }
}

.visible {
  &.scroll-in-slide {
    &-left {
      .container {
        animation-name: slide-in-left;
      }
    }

    &-right {
      .container {
        animation-name: slide-in-right;
      }
    }

    &-top {
      .container {
        animation-name: slide-in-top;
      }
    }

    &-bottom {
      .container {
        animation-name: slide-in-bottom;
      }
    }
  }
}

.scroll-out-slide {
  &-left {
    .container {
      animation-name: slide-out-left;
    }
  }

  &-right {
    .container {
      animation-name: slide-out-right;
    }
  }

  &-top {
    .container {
      animation-name: slide-out-top;
    }
  }

  &-bottom {
    .container {
      animation-name: slide-out-bottom;
    }
  }
}

@keyframes slide-in-left {
  0% {
    left: -100%;
    top: 0;
  }

  100% {
    left: 0;
    top: 0;
  }
}

@keyframes slide-in-right {
  0% {
    left: 100%;
    top: 0;
  }

  100% {
    left: 0;
    top: 0;
  }
}

@keyframes slide-in-top {
  0% {
    top: -100%;
    left: 0;
  }

  100% {
    top: 0;
    left: 0;
  }
}

@keyframes slide-in-bottom {
  0% {
    top: 100%;
    left: 0;
  }

  100% {
    top: 0;
    left: 0;
  }
}

@keyframes slide-out-left {
  0% {
    left: 0;
    top: 0;
  }
  100% {
    left: -100%;
    top: 0;
  }
}

@keyframes slide-out-right {
  0% {
    left: 0;
    top: 0;
  }
  100% {
    left: 100%;
    top: 0;
  }
}

@keyframes slide-out-top {
  0% {
    top: 0;
    left: 0;
  }
  100% {
    top: -100%;
    left: 0;
  }
}

@keyframes slide-out-bottom {
  0% {
    top: 0;
    left: 0;
  }
  100% {
    bottom: 100%;
    left: 0;
  }
}
