import { Component, Prop, h, Element, Host, FunctionalComponent, State, Watch } from '@stencil/core';
import { ContainerAnimation } from '../../../interfaces/components/layout/container/types';

@Component({
  tag: 'rwc-container',
  styleUrl: 'container.scss',
  shadow: true
})
export class Container {
  /**
   * Reference to the host element
   */
  @Element() host: HTMLElement;

  /**
   * The intersection observer of the component
   */
  private intersectionObserver: IntersectionObserver;
  
  /**
   * Has the component been completely initialized
   */
  private initialized: boolean = false;

  /**
   * The animation to play when the container is scrolled into viewport
   */
  @Prop() scrollIntoAnimation: ContainerAnimation;

  /**
   * The animation to play when the container is scrolled out of viewport
   */
  @Prop() scrollOutAnimation: ContainerAnimation;

  /**
   * Enable the opacity animation for the scroll animation
   */
  @Prop() animateOpacity: boolean = true;

  /**
   * The duration of the animation in milliseconds
   */
  @Prop({ reflect: true }) animationDuration: number = 300;
  
  /**
   * The intersection threshold for the animation to trigger \
   * What percentage of the element must be in the viewport to trigger the animation
   */
  @Prop() visibilityThreshold: number = 0;

  /**
   * Handle the animation duration change \
   * Sync the animation duration with the css animations
   */
  @Watch('animationDuration')
  onAnimationDurationChange(newValue: number): void {
    this.setAnimationDuration(newValue);
  }

  /**
   * Is the host element currently inside the viewport
   */
  @State() isVisible: boolean = false;

  /**
   * Called when the component is to be loaded for the first time
   */
  componentWillLoad(): void {
    this.setAnimationDuration(this.animationDuration);
  }

  /**
   * Called when the component finishes loading for the first time
   */
  componentDidLoad(): void {
    this.initIntersectionObserver();
  }

  /**
   * Initialize the intersection observer \
   * Detects if the host element's height percentage defined by visibilityThreshold is inside the viewport. 
   */
  private initIntersectionObserver(): void {
    this.intersectionObserver = new IntersectionObserver(
      (entries: IntersectionObserverEntry[]) => this.handleIntersection(entries),
      { threshold: this.visibilityThreshold }
    );
    this.intersectionObserver.observe(this.host);
  }

  /**
   * Handle the intersection state change
   */
  private handleIntersection(entries: IntersectionObserverEntry[]): void {
    if (entries[0].isIntersecting) {
      this.isVisible = true;
      // If no scroll out animation defined, remove the observer
      if (this.scrollOutAnimation === 'none' || !this.scrollOutAnimation) {
        this.removeIntersectionObserver();
      }
    } else {
      this.isVisible = false;
      // If no scroll into animation defined, remove the observer
      if (this.scrollIntoAnimation === 'none' || !this.scrollIntoAnimation) {
        this.removeIntersectionObserver();
      }
    }
  }

  /**
   * Remove the intersection observer from the component
   */
  private removeIntersectionObserver(): void {
    if (this.initialized && this.intersectionObserver || (!this.hasScrollIntoAnimation && !this.hasScrollOutAnimation && this.isVisible)) {
      this.intersectionObserver.disconnect();
      this.intersectionObserver = null;
    }
    this.initialized = true;
  }

  /**
   * Set the animation duration on the host's root element to sync with the css
   */
  private setAnimationDuration(duration: number): void {
    this.host.style.setProperty('--_animation-duration', `${ duration }ms`);
  }
  
  /**
   * Does the component has the scroll into animation set
   */
  private get hasScrollIntoAnimation(): boolean {
    return this.scrollIntoAnimation && this.scrollIntoAnimation !== 'none'; 
  }
  
  /**
   * Does the component has the scroll out animation set
   */
  private get hasScrollOutAnimation(): boolean {
    return this.scrollOutAnimation && this.scrollOutAnimation !== 'none';
  }

  /**
   * Render the component
   */
  render(): FunctionalComponent {
    return (
      <Host>
        <div
          class={ {
            wrapper: true,
            visible: this.isVisible,
            'animate-opacity': this.animateOpacity,
            [`scroll-in-${ this.scrollIntoAnimation }`]: true,
            [`scroll-out-${ this.scrollOutAnimation }`]: true,
          } }
        >
          <div class="container">
            <slot />
          </div>
        </div>
      </Host>
    );
  }
}
